<template>
    <div>
        

    <div class="teacherDiv">
    <div class="setleft">
                        <div title=""> 
                            <div id="preview_div" style="width: 100%; height: 100px; border: 1px solid #b6ff00; cursor: pointer;" @click="previewFunc();">
                            <img id="preview_img" style="width:100%;height:100%;"/>
                            <input id="upload_img" type="file" style="width:0px;height:0px;" />
                            </div>
                         </div>
    </div>
    <div  class="setleft2">
        <table class="teachertable">
            <tr>
                <td>
                    
                </td>
                 <td>
                    <input class="myinput" v-model="number" placeholder="">
                </td>
                <td>
                </td>
                  <td>
                   
                </td>
            </tr>
             <tr>
                <td>
                    
                </td>
                 <td>
                    <input class="myinput" v-model="name" placeholder="">
                </td>
                  <td>
                </td>
                  <td>
                </td>
            </tr>
             <tr>
                <td>
                    
                </td>
                  <td>
                      <input class="myinput" v-model="subject" placeholder="">
                </td>
                <td>
                    
                </td>
                 <td>
                   <input v-model="tel" class="mytel" type="tel" placeholder="">
                </td>
                <td>
                     <img class="telImg" src="../../../static/imgs/me_kfphone.png" alt="">
                </td>
            </tr>
        </table>
    </div>
    </div>






        <el-card class="box-card">
            <div slot="header" class="clearfix">
            <div id="profile"></div>
            </div>
            <div id='conetent'>
                <el-input
                class="myInput"
                type="textarea"
                :autosize="{ minRows: 2, maxRows: 4}"
                placeholder=""
                v-model="conetent">
                </el-input>
            </div>
        </el-card>

        <el-card class="box-card">
            <div slot="header" class="clearfix">
            <div id="profile"></div>
            </div>
            <div id='conetent'>
               <el-input
               class="myInput"
                type="textarea"
                :autosize="{ minRows: 2, maxRows: 4}"
                placeholder=""
                v-model="educationInfo">
                </el-input>
            </div>
        </el-card>

        <el-card class="box-card">
            <div slot="header" class="clearfix">
            <div id="profile"></div>
            </div>
            <div id='conetent'>
                 <el-input
                 class="myInput"
                type="textarea"
                :autosize="{ minRows: 2, maxRows: 4}"
                placeholder=""
                v-model="secusscase">
                </el-input>
            </div>
        </el-card>
  <el-button class="mybtn" @click="submit()" type="success" round></el-button>
    </div>
</template>

<style scoped>


.mytel
{
    width: 90px;
}
.myinput
{
   width: 50px
}
.teachertable
{
    font-size: 10px;
    text-align: right;
    width: 100%;
    height: 100px;
}
.el-upload-dragger
{
    width: 100px !important;
    height: 100px !important;
}
.el-card__body
{
    padding: 2px!important;
}
.teacherDiv
{
    overflow: hidden;
}
.teachImg
{
    width: 100%;
    height: 100px;
}
.upload-demo
{
    width: 100%;
    height: 100%;
}
.setleft
{
    float: left;
    width: 25%; 
}
.setleft2
{
    background-color:lightcyan;
    float: left;
    width: 70%;
   
}
.telImg
{
    width: 20px;
    height: 20px;
}
a
{
    text-decoration: none;
}




  .text {
    font-size: 14px;
  }
  .myInput
  {
      width: 100%;
  }
#profile,#conetent
{
    text-align: left;
    margin-left: 3px;
}
#conetent
{
     font-size: 12px;
    text-align: left;
     width: 100%;
}
  .item {
    margin-bottom: 18px;
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    margin-top: 8px;
    width: 100%;
  }
  .mybtn
  {
      width: 100%;
  }
</style>

<script>
import teacherBlock from '@/components/editTeacherBlock'
 var mpreviewFunc,mcreateFileObj;
export default {
    data()
    {
    return{conetent:'',educationInfo:'',secusscase:'', number:'',
     name:'',
     subject:'',
     tel:''}
    },
    components:{teacherBlock},
    methods:{

 previewFunc()
    {
        document.getElementById("upload_img").click();
    },
    createFileObj(filePath)
    {
        var url = null;
        if (window.createObjectURL != undefined) {
            url = window.createObjectURL(filePath);
        } else if (window.URL != undefined) {
            url = window.URL.createObjectURL(filePath);
        } else if (window.webkitURL != undefined) {
            url = window.webkitURL.createObjectURL(filePath);
        }
        else {
 
        }
        return url;
    },
    submit()
    {


        var file= document.getElementById("upload_img").files[0]
        if(file==null)
        {
            alert("");
            return
        }
      var api = this.baseUrl;
      let url = api + "/TInfo/add";
      var teacherInfo = {};
      teacherInfo.profile = this.conetent;
      teacherInfo.experience = this.educationInfo;
      teacherInfo.seccessCase = this.secusscase;
      teacherInfo.name=this.name;
        teacherInfo.subject=this.subject;
        teacherInfo.tel=this.tel;
        teacherInfo.number=this.number;
       

       var data=new FormData;
			
			
		let stringify = JSON.stringify(teacherInfo);
		data.append("file",document.getElementById("upload_img").files[0]);
		data.append('teacher',stringify);
         var title='';
        this.axios.post(url, data,{headers: {'Content-Type': 'multipart/form-data'}})
        .then(function (response) {
          if(response.data.code==100)
          {
              
              title='';

              alert(title);
          }
          else
          {
               title='';

                alert(title);

          }


        })
        .catch(function (error) {
         
            alert(error);
        });
 


    },
    init()
    {
             //
        document.getElementById("upload_img").onchange = function () {
            var sender = this;
            if (!sender.value.match(/.jpg|.gif|.png|.bmp/i)) {
                alert('');
                return false;
            }
    
            var objPreviewImg = document.getElementById('preview_img');
            var objPreviewDiv = document.getElementById('preview_div');
    
            if (navigator.userAgent.indexOf("MSIE") > -1) {
                //IE
                try {
                    objPreviewImg.src = this.createFileObj(this.files[0]);
                }
                catch (e) {
                        //ie7
                    this.select();                                  //
                    top.parent.document.body.focus();                //iframeiframe
                    this.blur();                                   //
                    var src = document.selection.createRange().text;          //IE 11:document.selection ---  window.getSelection  
                    document.selection.empty();
                    objPreviewImg.style.display = "none";                 //img
                    objPreviewDiv.style.filter = "progid:DXImageTransform.Microsoft.AlphaImageLoader(sizingMethod=scale)";
                    objPreviewDiv.filters.item("DXImageTransform.Microsoft.AlphaImageLoader").src = src;
                }
            } else {
                //chrome firfox
                objPreviewImg.src = mcreateFileObj(this.files[0]);
            }
        }
        }
    
   
    },
    mounted(){
        mpreviewFunc=this.previewFunc;
        mcreateFileObj=this.createFileObj;
        this.init();
    }
}
</script>


