<template>
  <div>
    <el-card class="box-card">
      <el-input v-model="tel" type="tel" placeholder=""></el-input>
      <el-input type="password" v-model="pwd" placeholder=""></el-input>
      <el-input type="password" v-model="secondPwd" placeholder=""></el-input>
      <el-input type="nick" v-model="nick" placeholder=""></el-input>

      <el-button type="success" class="btn" round @click="register()"></el-button>
    </el-card>
  </div>
</template>

<style scoped>
.btn {
  width: 100%;
}
.text {
  font-size: 14px;
}

.item {
  padding: 18px 0;
}

.box-card {
  width: 100%;
}
</style>

<script>
export default {
  data() {
    return {
      tel: "",
      pwd: "",
      secondPwd: "",
      nick: "",
    };
  },
  methods: {
    register() {
      if (this.pwd != this.secondPwd) {
        this.$alert("", "", {
          confirmButtonText: "",
          callback: (action) => {
            this.$message({
              type: "info",
              message: `action: ${action}`,
            });
          },
        });
      }
      var api = this.baseUrl;
      let url = api + "/addUser";
      var user = {};
      user.phoneNumber = this.tel;
      user.pwd = this.pwd;
      user.nick = this.nick;
      user.name = "";

      
        this.axios.post(url, user)
        .then(function (response) {
           
            if(response.data.code==100)
            {
                   alert("");
            }
            else
            {
                  alert("");
            }


        })
        .catch(function (error) {
         
           alert("");


        });
    },
  },
};
</script>


