<template>
<div>
     	<input  id="search" class="bar7" type="text" placeholder="..."/>
    <div id="myChart"></div>
</div>
   
</template>
<style scoped>
	.bar7 {
		height: 40px;
		margin: 0 auto;
		width:90%;
		border-radius:42px;
		border:2px solid #324B4E;
		background:#F5F5F5;
		padding-left: 20px;
			}

</style>
<script>
import echarts from 'echarts'	// echarts
export default {
    name: 'LineChart',
    data () {
        return {
            echartsOption: {	// echarts
                legend: {	//
                    data: ['', '', '', '']
                },
                xAxis: {
                    type: 'category',
                    data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],   // x
                    name: '',   // x
                    nameTextStyle: {	// x
                        fontWeight: 600,
                        fontSize: 18
                    }
                },
                yAxis: {
	                type: 'value',
	                name: '',   // y
	                nameTextStyle: {	// y
	                    fontWeight: 600,
	                    fontSize: 18
	                }
                },
                tooltip: {   //
                    trigger: 'axis'
                },
                series: [	//
                    {
                        name: '',
                        data: [820, 932, 901, 934, 1290, 1330, 1320],
                        type: 'line'
                    },
                    {
                        name: '',
                        data: [620, 711, 823, 934, 1445, 1456, 1178],
                        type: 'line'
                    },
                    {
                        name: '',
                        data: [612, 920, 1140, 1160, 1190, 1234, 1321],
                        type: 'line'
                    },
                    {
                        name: '',
                        data: [234, 320, 453, 567, 789, 999, 1200],
                        type: 'line'
                    }
                ],
            }
        }
    },
    mounted () {
        let myChart = echarts.init(document.getElementById('myChart'), 'light')	// echarts, themelight
        myChart.setOption(this.echartsOption)	// echarts
    }
}
</script>

<style>
#myChart{
  width: 100%;
  height: 500px;
  margin: 0 auto;
  margin-top: 5%;
}
</style>


